import HandCamera from '../HandCamera';
import fp from 'fingerpose';
import { useRef, useState, useEffect } from 'react';
import { rock, paper, scissor } from './gesture';

const style = {
  position: "absolute",
  top: 0,
  left: 0,
  right: 0,
  zIndex: -1,
  width: 640,
  height: 480
};

const emojiMap = {
  'scissor': 'âœŒðŸ»',
  'rock': 'âœŠ',
  'paper': 'âœ‹',
  '': '',
}

const Game = (props) => {
  const estimator = useRef(null);
  const [gesture, setGesture] = useState();

  function onHandEstimate(predictions) {
    const estimated = estimator.current.estimate(predictions.landmarks, 7.5);
    if (estimated.gestures.length > 0) {
      const best = estimated.gestures.reduce((a, b) => {
        return (a.confidence > b.confidence) ? a : b;
      })
      if (best.confidence < 9) {
        return;
      }
      if (gesture !== best.name) {
        setGesture(best.name);
        setInterval(() => {
          setGesture('');
        }, 1000)
      }
    }
  }

  useEffect(() => {
    estimator.current = new fp.GestureEstimator([
      rock, paper, scissor
    ]);
  }, []);
    
  return (
    <>
    <div style={{position: 'fixed', left: 32}}>
      <h1>{emojiMap[gesture]}</h1>
    </div>
    <HandCamera style={style} backend='webgl' onHandEstimate={onHandEstimate} debug={false} />
    </>
  );
}

export default Game;