import { useEffect, useRef } from 'react';
import Webcam from "react-webcam";
import { drawHand } from './utils';
import useModel from '../HandPose';

const HandCamera = (props) => {
  const webcamRef = useRef(null);
  const canvasRef = useRef(null);
  const detector = useModel({backend: props.backend});

  function onDetection(predictions) {
    let ctx = null;
    predictions.forEach((prediction) => {
      props.onHandEstimate(prediction);
      if (props.debug) {
        if (ctx === null) {
          ctx = canvasRef.current.getContext("2d");
          ctx.translate(canvasRef.current.width, 0);
          ctx.scale(-1, 1);
        }
        drawHand(prediction.landmarks, ctx);
      }
    })
  }

  function detect() {
    if (
      typeof webcamRef.current !== "undefined" &&
      webcamRef.current !== null &&
      webcamRef.current.video.readyState === 4
    ) {
      const video = webcamRef.current.video;
      const videoWidth = video.videoWidth;
      const videoHeight = video.videoHeight;

      webcamRef.current.video.width = videoWidth;
      webcamRef.current.video.height = videoHeight;

      canvasRef.current.width = videoWidth;
      canvasRef.current.height = videoHeight;
      
      const hands = detector.current;
      if (hands !== null) {
        hands.estimateHands(video).then(
          (predictions) => {
            if (predictions.length > 0) {
              onDetection(predictions);
            }
          }
        );
      }
    }
  }

  function onAnimate() {
    detect();
    requestAnimationFrame(onAnimate);
  }

  useEffect(() => {
    onAnimate();
  }, [])
  
  return (
    <div>
      <Webcam ref={webcamRef} mirrored style={props.style} />
      <canvas ref={canvasRef} style={props.style} />
    </div>
  )
}

export default HandCamera
